@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;1,400;1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper {
    display: flex;
    flex-direction: column;
    background-color: #5650f4;
    margin: 0 auto;
    position: relative;
}
.title {
    align-self: center;
    z-index: 9;
    margin-top: -1rem;
    border: 3px solid white;
    background-color: white;
    border-radius: 15px;
    font-size: 1.5rem;
    position: absolute;
}
.results {
    font-size: 1.4rem;
    padding: 2rem;
    background-color: #d9d7ed;
    min-height: 100vh;
}
.input-word {
    
    color: white;
    padding: 0 0 35px 0;
    background-color: #5650f4;
    font-style: italic;
    
    font-size: 3rem;
    font-weight: 400;
}
body {
    background-color: #5650f4;
}
.form-wrapper {
    font-size: 23px;
}
div, a {
    color: black;
}
main {
    background-color: #d2d2d2;
}
form {
    padding: 25px;
    background-color: #5650f4;
}
label {
    color: white;
}
.user-word-wrapper {
    position: relative;
    padding: 0 9px;
}
.user-word-wrapper label {
    position: absolute;
    font-size: 20px;
    transform: translate(0, 16px) scale(1);
    transform-origin: top left;
    transition: all .1s ease-in-out;
}

.user-word-wrapper input {
    outline: 0;
    border: none;
    border-bottom: 1px solid white;
    background-color: #5650f4;
    color: white;
    font-size: 20px;
    padding: 16px 0 0 0;
}

.user-word-wrapper.active label {
    transform: translate(0, 4px) scale(.75);
}
select {
    background-color: #d9d7ed;
    height: 40px;
    width: 60vw;
    text-align: center;
}

#options-label {
    color: white;
}

#search-button {
    color: #5650f4;
    background-color: #5650f4;
    border: 2px solid #5650f4;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-size: 50px;
    height: 50px;
    width: 50px;
    margin-top: 10px;
}
body {
    background-color: #5650f4;
}
html {
  background-color: #5650f4;
}
.App {
  text-align: center;
  background-color: #5650f4;
  min-height: 100vh;
  font-family: 'Playfair Display', serif;
  font-size: 20px;
}
body {
  background-color: #5650f4;
}
h1{
  color: white;
}

img {
  height: 150px;
  width: 150px;
}
